
//属性的样式表
.sx-navbar {
    width: 100%;
    overflow: hidden;
    position:fixed;
    top:0px;
    z-index:99;
    padding:10px 0px;
    background:#fff; 
    @include clearspace();
    ul {
        li+li {
            margin-left: 0.77%;
            @include normalspace();
        }
        li {
            float: left;
            width: 16%;
            position: relative;
            z-index: 99;
            box-sizing: border-box;
            background: #efefef;
            line-height: 60*$px;
            color: $theme;
            font-size: 17*$px;
            text-align: center;
            a {
                display: block;
                height: 60px;
                border: 1px solid #e7e7e7;
                // margin-right:19px;
                
            }
 
            &.active,&:hover {
                background: $theme;
                color: #fff;
            } 
            &.disabled {
                background:#e9e9e9;
                &:hover {
                    .disabled-con {
                        display: block;
                    }
                }
            }
            .icon {
                    vertical-align: -1px;
                    margin-right: 12px;
                    font-size: 16*$px;
                }
        }
    }
 
    .disabled-con {
        display: none;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        background: #999;
        color: #fff;
        cursor: default;
         
    }
}

 .sx-active{
     background:$theme;
     color:#fff;
}
.sx-main {
    width: 100%;
