//运维的样式表
.head{
	height:80px;
	position:relative;
	border:none;
	.logo{
		background:url("../images/yunwei/yunwei-logo.png") center left no-repeat;
		height:100%;
	}
	.yw-h-right{
		position:absolute;
		top: 17px;
		right: 20px;
		font-size: 14px;
		.col-xs-0{
			label{
				display: inline-block;
				width: 100px;
				padding-right:20px;
				text-align: right;
				line-height: 30px;
			}
			.orange{
				color: #fff;
                background: #e49f26;
                &:hover {
                      background: #d69420;
                   }
			}

		}
		.ml20{
			margin-left:20px;
		}
	}
	.form{
		margin:0px auto;
	}
}
.banner{
	height: 336px;
    background: url(../images/yunwei/yunwei-banner.jpg) center no-repeat;
}
.con{
	width: 1000px;
	min-height: 250px;
	margin: 0 auto;
	.col-xs-yw{
		width:20%;
		display:inline-block;
		overflow:hidden;
		.yw-funmenu-btn{
				display: inline-block;
				zoom: 1;
				*display: inline;
				padding: 20px 30px 10px 30px;
				font-size: 20px;
				line-height: 60px;
				border: 1px solid #fff;
		}
	}
}
.lx-con{
	width:1140px;
	margin-top:49px;
}
.col-xs-wap {
    word-spacing: -.3333em;
}
.tc{
	text-align:center;
}

.ebus,.dianqu,.nbjyjk,.dwdfx,.ywjs{
	display: block;
	width: 126px;
	height: 146px;
	vertical-align: middle;
    background: url(../images/yunwei/yw.png) no-repeat;

}
.ebus{
    background-position: -31px -32px;
}
.dianqu{
    background-position: -161px -32px;
}
.nbjyjk{ 
    background-position: -291px -32px;
}
.dwdfx{ 
    background-position: -421px -32px;
}
.ywjs{ 
    background-position: -551px -32px;
}
//运维的样式表
.sq-main{
	background:#fff;
	/*padding:12px;*/
	margin-top:76px;
	.sq-item{
		border:1px solid #e0e0e0;
		position:relative;
		margin-bottom:10px;
		.sq-title{
			width:135px;
			height:38px;
			background:url("#{$image-src}/zy/ziyuanshuxin/sq-title.png") no-repeat;
			position:absolute;
			top:0px;
			left:-2px;
			text-align:center;
			line-height:32px;
			color:#fff;
			font-size:14px;
			// &::before{
			// 	content:"";
			// 	border-top:20px solid transparent;
			// 	border-right:20px solid #f3f3f3;
			// 	border-bottom:20px solid transparent;
			// 	position:absolute;
			// 	right:0px;
			// 	top:0px;
			// }
		}
		.sq-title-jb{
			background-position: 2px 0px;
			// background:#5097bf;
		}
		.sq-title-yw{
			background-position: 2px -42px;
			// background:#6f5dd8;
		}
		.sq-title-gc{
			background-position: 2px -85px;
			// background:#e79e45;
		}
		.sq-title-qt{
			background-position: 2px -131px; 
			// background:#1e9fc2;
		}
		.sq-xq{
			position:absolute;
			right:9px;
			top:9px;
			color:#7a69dc;
			font-size:16px;
			cursor:pointer;
		}
		.sq-content{
			width:100%;
			margin-top:52px;
			padding-left:66px;
			.sq-left{
				width:50%;
				float:left;
				.sq-img{
					text-align:center;
					display:inline-block;
					margin:16px 42px;
					color:#5097bf;
					p{
						margin-top:22px;
						font-size:14px;
					}
				}
				.sq-left-content{
					font-size:16px;
					span{
						color:#898989;
						margin-right:36px;
						width:14%;
						display:inline-block;
				    }
				    ul{
				    	li{
				    		margin-bottom:16px;
				    		color:#000;
				    	}
				    }
				}
			}
			.sq-right{
				width:50%;
				float:right;
				.sq-right-content{
					font-size:16px;
					span{
						color:#898989;
						margin-right:24px;
						width:14%;
						display:inline-block;
				    }
				    ul{
				    	li{
				    		margin-bottom:16px;
				    	}
				    }
				}
			}
	    }
	}
} 
