}
.sq-box{
	background: #efefef;
	border: 1px solid #e0e0e0;
	padding-bottom:5*$px;
}
.sq-img-wap{ 
	width:100%;
	 min-height: 180*$px;
	 @include clearspace;
}
.sq-img-l{    
	 width:50%;
	 padding:4*$px 0 15*$px 0px;
	 text-align: left;
	 box-sizing:border-box;
	 @include inline-block;
	 vertical-align: middle;
	 // padding: 20*$px 10*$px  20*$px  120*$px ; 
	img{
		height: 130*$px;
		width: auto;
		display:inline-block;
	}
}
.sq-img-r {     
	 display:inline-block;
	 vertical-align: middle;
	 width: 50%; 
	.row {
	  
		&.hide{
			display: none;
		}
	}
}
.sq-img{ 
	text-align:center;
	position: absolute;
	top:20*$px;
	bottom:40*$px; 
	@include inline-block;
	color:#5097bf;
	p{
		margin-top:22px;
		font-size:14px;
		text-align: center;
	}
	img{
		min-height:100px;
		width: auto;
		height:80%;
		max-height: 160px;
	}
}
.sq-col-xs{
	label{
		width:169px;
	}
}
.sq-img-content{
	margin-right:100px;
	overflow:hidden;
	background:#fff;
	width:400px;
}
.sq-img-left{
	width:82%;
	float:left;
	text-align:center;
	padding-top:60px;
	p{
		color:#5097bf;
		font-size:14px;
		margin-top:26px;
	}
}
.sq-img-right{
	width:14%;
	float:right;
	text-align:center;
	border:2px solid #e1e1e1;
	ul li{
		width:100%;
		// border-bottom:2px solid #e1e1e1;
		line-height:60px;
		color:#8d918f;
		cursor:pointer;
		img{
			max-height:58px;
			width:auto;
			overflow:hidden;		
		}
	}
	.sq-nav-li{
		height:180px;
		position:relative;
		overflow:hidden;
	}
	.sq-icon{
		width:100%;
		height:36px;
		line-height:36px;
		background:#e0e0e0;
		color:#8d918f;
   }
   .sq-add{
   		background:#f3f3f3;
   		height:60px;
   		line-height:60px;
   		position:relative;
   		z-index:36;
   }
}
.sq-ul{
	
	overflow:hidden;
	li{
		width:auto;
		height:60px;
	}
